













export default {
	data() {
		return {
			list:[]
		};
	},
	onLoad() {
		this.$utils.tRequest({
			url: this.$webUrl+"home_notice",
			method: 'GET',
			data: {},
			success: res => {
				if(res.code == 0){
					this.list = res.data;
				}
			},
			fail: () => {},
			complete: () => {}
		})
	},
	methods:{
		toDetail:function(){
			uni.navigateTo({
				url: './notifyDetail/notifyDetail',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		}
	}
}
